.scroll-up-button {
  position        : fixed;
  bottom          : 90px;
  right           : 25px;
  width           : 40px;
  height          : 40px;
  display         : flex;
  justify-content : center;
  align-items     : center;
  background-color: #3c3c3c;
  color           : #fff;
  border-radius   : 50%;
  font-size       : 24px;
  cursor          : pointer;
  opacity         : 0;
  pointer-events  : none;
  transition      : opacity 0.3s ease;
}

.scroll-up-button.show {
  opacity       : 1;
  pointer-events: auto;
}